.page {
    display: grid;
    height: 100vh;
    width: 100vw;
    background-color: rgb(10, 10, 10);
    z-index: 0;
}

.mainText {
    place-self: center;
    color: white;
    border: white .3em solid;
    text-align: center;
}

.header {
    font-size: 3em;
    letter-spacing: 4px;
    margin: 0 .5em;
}